import React from "react";
import "./Highlights.css";

export default class Highlights extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    const data = this.props.data.slice(0, 3);
    console.log(data);
    return (
      <div className="highlights">
        <h2>Destaques</h2>
        <div>
          {data.map(news => (
            <article className="highlightNewsItem" key={`highlights${news.id}`}>
              <h3>{news.title}</h3>
              <div className="imgDiv">
                <img src={news.imgs[0]} alt={news.title} title={news.title} />
              </div>
            </article>
          ))}
        </div>
      </div>
    );
  }
}
