import React from "react";
import "./MainNews.css";

export default class MainNews extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const data = this.props.data;
    return (
      <article className="MainNews" {...this.props}>
        <a href={`noticia/${data.id}`}>
            <img src={data.imgs[0]} alt={data.title} title={data.title} />
        </a>
        <a href={`noticia/${data.id}`}>
          <h2>{data.title}</h2>
        </a>
        <p>{data.description}</p>
      </article>
    );
  }
}
