import React from "react";
import Nav from "./Navbar";
import "./AppHeader.css";

export default class AppHeader extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};

    this.onClickResponsiveMenuBtn = this.onClickResponsiveMenuBtn.bind(this);
  }

  onClickResponsiveMenuBtn(event) {
    this.setState({ showResponsiveMenu: !this.state.showResponsiveMenu });
  }

  render() {
    return (
      <header>
        <div className="flex innerTitle">
          <div className="flex headerTitle">
          <button className="toggleResponsiveMenu" onClick={this.onClickResponsiveMenuBtn}></button>
            <div className="flex headerLeftBlock mobile-only">
              <a className="fa fa-bars" href="#" />
            </div>
            <div className="flex headerLeftBlock desktop-only">
              <span className="headerSocialNetworks">
                <a
                  className="fa fa-instagram"
                  href="http://www.instagram.com/jornalnoalvo/"
                >
                  <span className="hidden">Instagram</span>
                </a>
                <a
                  className="fa fa-facebook"
                  href="https://www.facebook.com/NoAlvoComunicacaoDeVerdade/"
                >
                  <span className="hidden">Facebook</span>
                </a>
                <a
                  className="fa fa-whatsapp"
                  href="https://api.whatsapp.com/send?phone=5515996266044"
                >
                  <span className="hidden">Whatsapp</span>
                </a>
              </span>
              <a href="/contato/">Assine nosso jornal</a>
            </div>
            <div className="flex headerCenterDiv">
              <a href="/" rel="home">
                <img
                  src="/images/logo.png"
                  alt="Jornal No Alvo"
                  title="Jornal No Alvo Aqui a Comunicação é de Verdade"
                  id="logoImg"
                />
              </a>
            </div>
            <div className="flex headerRightBlock mobile-only">
              <a className="fa fa-search" href="#" />
            </div>
            <div className="flex headerRightBlock desktop-only">
              <a href="/quem-somos/">Quem Somos</a>
              <a href="/publicidade/">Publicidade</a>
              <a href="/contato/">Contato</a>
            </div>
          </div>
        </div>
        <Nav style={this.state.showResponsiveMenu && {display: "block"}}/>
      </header>
    );
  }
}
