import React, { useLayoutEffect, useRef, createRef, useEffect } from "react";

export default class ResponsiveGrid extends React.Component { 
    ref;
    key;
    currentHeight = 0;

    constructor(props) {
        super(props);

        this.key = this.props.key;

        this.ref = React.createRef();
    }

    componentDidUpdate() {
        this.calculateHeight();
    }

    calculateHeight() {
        const ref = this.ref;
        if (ref.current) {
            const height = ref.current.clientHeight
            const scroll = ref.current.scrollHeight
             if (scroll > height) {
               const newRowHeight = scroll
               const detail = {elem: this.ref.current, newRowHeight, key: this._reactInternalFiber.key};
               document.dispatchEvent(new CustomEvent('resizeGrid', { detail }));
               //console.log({height, scroll})
               // –> update this grid item { h: newRowHeight }
             }
       }
      }

    render() {
        return (
            <div ref={this.ref} {...this.props}>
                {this.props.children}
            </div>
        )
    }
}