import React from "react";
import "./Navbar.css";
import NavMenuItem from "./NavMenuItem";

export default class Nav extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      mobileMedia: window.matchMedia("(max-width: 1000px)").matches
    };

    this.handleScroll = this.handleScroll.bind(this);
    this.setPos = this.setPos.bind(this);
  }

  handleScroll() {
    this.setState({ scroll: window.scrollY });
  }

  setPos() {
    const el = document.querySelector("nav");
    const headerEl = document.querySelector("header");
    if (el) {
      const style = el.currentStyle || window.getComputedStyle(el);
      const marginTop = parseInt(style.marginTop);
      const top = el.offsetTop;
      const windowWidth = window.innerWidth;
      this.setState({
        top,
        marginTop,
        el,
        height: el.offsetHeight,
        headerHeight: headerEl.offsetHeight,
        windowWidth
      });
    }
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
    window.addEventListener("load", this.setPos);
    window.addEventListener("resize", this.setPos);
    window
      .matchMedia("(max-width: 1000px)")
      .addListener(e => this.setState({ mobileMedia: e.matches }));
  }

  componentDidUpdate() {
    this.state.mobileMedia && this.state.el
      ? (document.body.style.paddingTop = `${this.state.headerHeight}px`)
      : this.state.scroll > this.state.top
      ? (document.body.style.paddingTop = `${this.state.height}px`)
      : (document.body.style.paddingTop = 0);
  }

  render() {
    let lista = [
      { name: "Notícias", route: "/categoria/noticias" },
      { name: "Opinião", route: "/categoria/opiniao" },
      { name: "Política", route: "/categoria/politica" },
      { name: "Polícia", route: "/categoria/policia" },
      {
        name: "Região", route: "/categoria/regiao",
        submenu: [
          { name: "Angatuba", route: "/categoria/angatuba-sp" },
          { name: "Apiaí", route: "/categoria/apiai-sp" },
          { name: "Barão de Antonina", route: "/categoria/barao-de-antonina-sp" },
          { name: "Bom Sucesso de Itararé", route: "/categoria/bom-sucesso-de-itarare-sp" },
          { name: "Buri", route: "/categoria/noticias/buri-sp" },
          { name: "Campina de Fora", route: "/categoria/campina-de-fora-sp" },
          { name: "Coronel Macedo", route: "/categoria/coronel-macedo-sp" },
          { name: "Guapiara", route: "/categoria/guapiara-sp" },
          { name: "Holambra", route: "/categoria/holambra-sp" },
          { name: "Ipaussu", route: "/categoria/ipaussu-sp" },
          { name: "Itaberá", route: "/categoria/itabera-sp" },
          { name: "Itaí", route: "/categoria/itai-sp" },
          { name: "Itapeva", route: "/categoria/itapeva-sp" },
          { name: "Itaporanga", route: "/categoria/itaporanga-sp" },
          { name: "Itararé", route: "/categoria/itarare-sp" },
          { name: "Nova Campina", route: "/categoria/nova-campina-sp" },
          { name: "Paranapanema", route: "/categoria/paranapanema" },
          { name: "Piraju", route: "/categoria/piraju" },
          { name: "Ribeirão Branco", route: "/categoria/ribeirao-branco" },
          { name: "Riversul", route: "/categoria/riversul" },
          { name: "Taquarituba", route: "/categoria/taquarituba" },
          { name: "Taquarivaí", route: "/categoria/taquarivai-sp" }
        ]
      },
      { name: "Economia", route: "/categoria/economia" },
      { name: "Evidências", route: "/categoria/evidencias" },
      { name: "Esporte", route: "/categoria/esportes" },
      { name: "Cultura", route: "/categoria/cultura" },
      { name: "Agronegócio", route: "/categoria/agronegocio" }
    ];

    if (this.state.mobileMedia) {
      lista = [
        { name: "Início" },
        { name: "Quem Somos" },
        { name: "Editorias", submenu: lista },
        { name: "Publicidade" },
        { name: "Fale Conosco" }
      ];
    }

    if(this.state.mobileMedia) {
      document.body.classList.add('noscroll')
  } else
      document.body.classList.remove('noscroll')

    lista = lista.map(item => (
      <NavMenuItem
        mobileMedia={this.state.mobileMedia}
        key={item.name}
        data={item}
      />
    ));
    return (
      <nav
        style={this.props.style ? this.props.style : null}
        role="navigation"
        className={
          this.state.scroll > this.state.top && this.state.windowWidth > 1000
            ? "fixed-nav"
            : ""
        }
      >
        <ul>{lista}</ul>
      </nav>
    );
  }
}
