import React from "react";
import { Link } from "react-router-dom";

export default class NavMenuItem extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};

    this.onClick = this.onClick.bind(this);
    this.onMouseEnter = this.onMouseEnter.bind(this);
    this.onMouseOut = this.onMouseOut.bind(this);

    this.outInterval = undefined;
  }

  onMouseEnter(event) {
    if (!this.props.mobileMedia && this.props.data.submenu && !this.showSubmenu) {
      clearTimeout(this.outInterval);
      this.outInterval = null;
      this.setState({ showSubmenu: true });
    }
  }

  onMouseOut(event) {
    if (!this.props.mobileMedia && this.state.showSubmenu && this.outInterval == null) {
      const that = this;
      if (this.props.data.submenu) {
        this.outInterval = setTimeout(
          _ => that.setState({ showSubmenu: false }),
          1000
        );
      }
    }
  }

  onClick(event) {
      if(this.props.mobileMedia) {
        event.preventDefault();
        this.setState({showSubmenu: !this.state.showSubmenu});
      }
  }

  componentDidMount() {}

  render() {
    let submenu = null;
    let className = [];
    if(this.props.data.submenu)
        className.push('has-children');
    if (this.props.data.submenu && this.state.showSubmenu) {
        className.push('opened');
      submenu = (
        <ul>
          {this.props.data.submenu.map(item => (
            <NavMenuItem mobileMedia={this.props.mobileMedia} key={item.name} data={item} />
          ))}
        </ul>
      );
    }
    return (
      <li
        onMouseEnter={this.onMouseEnter}
        onMouseOver={this.onMouseEnter}
        onMouseOut={this.onMouseOut}
        onMouseLeave={this.onMouseOut}
        className={className.join(' ')}
      >
        <Link to={`${this.props.data.route}`} onClick={this.onClick}>
          {this.props.data.name}
        </Link>
        {submenu}
      </li>
    );
  }
}
