import React from "react";
import { WidthProvider, Responsive } from "react-grid-layout";
import "react-grid-layout/css/styles.css";
import layoutJson from "./layout.json";
import newsData from "./news.json";
import Banner from "../components/content/banner";
import MainNews from "../components/content/MainNews";
import Highlights from "../components/content/Highlights";
import ResponsiveGrid from "../components/content/ResponsiveGrid";

const ResponsiveReactGridLayout = WidthProvider(Responsive);

export default class ListNews extends React.Component {
  static get defaultProps() {
    return layoutJson;
  }

  handleResizeGrid(event) {
      console.log(event.detail);
  }

  componentDidMount() {
    document.addEventListener('resizeGrid', this.handleResizeGrid);
  }

  constructor(props) {
    super(props);

    this.handleResizeGrid = this.handleResizeGrid.bind(this);
  }

  render() {
    // layout is an array of objects, see the demo for more complete usage
    return (
      <ResponsiveReactGridLayout {...this.props} className="pageContent">
        <div key="banner">
          <Banner />
        </div>
        <div key="mainNews">
          <MainNews data={newsData[0]} />
        </div>
        <ResponsiveGrid key="highlights">
          <Highlights data={newsData} />
        </ResponsiveGrid>
        <div key="4">
          <span className="text">4</span>
        </div>
        <div key="5">
          <span className="text">5</span>
        </div>
      </ResponsiveReactGridLayout>
    );
  }
}
